import {Resource} from "~/models/Resource";
import {ResourcesJson} from "~/models/json/ResourcesJson";
import {resourcesJsonSchema} from "~/models/json/resourcesJsonSchema";
import {ModelIndex} from "~/ModelIndex";

export class ResourceIndex extends ModelIndex<Resource> {
  get resources(): readonly Resource[] {
    return this.models;
  }

  static fromJson(json: any) {
    const json_: ResourcesJson = resourcesJsonSchema.validateSync(json);
    return new ResourceIndex(
      json_.models
        .map(Resource.fromJson)
        .sort((left, right) => left.id.localeCompare(right.id)),
      json_.index
    );
  }
}
