import {PostalAddressJson} from "~/models/json/PostalAddressJson";

export class PostalAddress {
  constructor(private readonly json: PostalAddressJson) {}

  get country(): string | undefined {
    return this.json.country;
  }

  get displayString() {
    const commaSeparatedParts: string[] = [];
    if (this.postOfficeBoxNumber) {
      commaSeparatedParts.push(`PO Box ${this.postOfficeBoxNumber}`);
    } else if (this.streetAddress) {
      commaSeparatedParts.push(this.streetAddress);
    }
    if (this.locality) {
      commaSeparatedParts.push(this.locality);
    }
    if (this.region) {
      commaSeparatedParts.push(this.region);
    }
    if (this.country) {
      commaSeparatedParts.push(this.country);
    }
    const commaSeparatedString = commaSeparatedParts.join(", ");
    if (this.postalCode) {
      return commaSeparatedString + " " + this.postalCode;
    } else {
      return commaSeparatedString;
    }
  }

  get locality(): string | undefined {
    return this.json.locality;
  }

  get postalCode(): string | undefined {
    return this.json.postalCode;
  }

  get postOfficeBoxNumber(): string | undefined {
    return this.json.postOfficeBoxNumber;
  }

  get region(): string | undefined {
    return this.json.region;
  }

  get streetAddress(): string | undefined {
    return this.json.streetAddress;
  }
}
