import * as React from "react";
import {Resource} from "~/models/Resource";
import {Table} from "reactstrap";

export const ResourcesTable: React.FunctionComponent<{
  readonly resources: readonly Resource[];
}> = ({resources}) => {
  return (
    <Table striped>
      {/*<thead>*/}
      {/*  <tr>*/}
      {/*    <th scope="col">Name</th>*/}
      {/*  </tr>*/}
      {/*</thead>*/}
      <tbody>
        {resources
          .concat()
          .sort((left, right) => left.name.localeCompare(right.name))
          .map((resource) => (
            <tr key={resource.id}>
              <td style={{width: "40%"}}>
                {resource.url ? (
                  <a href={resource.url}>{resource.name}</a>
                ) : (
                  <span>{resource.name}</span>
                )}
                &nbsp;
                {resource.description ? <p>{resource.description}</p> : null}
              </td>
              <td style={{width: "20%"}}>
                {resource.address?.displayString ?? null}
              </td>
              <td style={{width: "10%"}}>
                {resource.telephone ? (
                  <a href={"tel:" + resource.telephone}>{resource.telephone}</a>
                ) : null}
              </td>
              <td style={{width: "20%"}}>
                {resource.openingHours ? (
                  <>Open:&nbsp;{resource.openingHours}</>
                ) : null}
              </td>
              <td style={{width: "10%"}}>{resource.email}</td>
            </tr>
          ))}
      </tbody>
    </Table>
  );
};
