import {Index} from "lunr";

export abstract class ModelIndex<ModelT extends {readonly id: string}> {
  private readonly modelsById: {[index: string]: ModelT};
  private readonly index: Index;

  protected constructor(
    protected readonly models: readonly ModelT[],
    index: any
  ) {
    this.modelsById = models.reduce((map, model) => {
      map[model.id] = model;
      return map;
    }, {} as {[index: string]: ModelT});
    this.index = Index.load(index);
  }

  search(queryString: string): readonly ModelT[] {
    return this.index
      .search(queryString)
      .map((result) => this.modelsById[result.ref]);
  }
}
