import {Document} from "~/models/Document";
import {DocumentsJson} from "~/models/json/DocumentsJson";
import {documentsJsonSchema} from "~/models/json/documentsJsonSchema";
import {ModelIndex} from "~/ModelIndex";

export class DocumentIndex extends ModelIndex<Document> {
  get documents(): readonly Document[] {
    return this.models;
  }

  static fromJson(json: any) {
    const json_: DocumentsJson = documentsJsonSchema.validateSync(json);
    return new DocumentIndex(
      json_.models
        .map(Document.fromJson)
        .sort((left, right) => left.id.localeCompare(right.id)),
      json_.index
    );
  }
}
