import * as React from "react";
import {Resource} from "~/models/Resource";
import {Fragment, useMemo} from "react";
import {Col, Container, Row} from "reactstrap";
import {ResourcesTable} from "~/applications/resources/ResourcesTable";

export const ResourcesTree: React.FunctionComponent<{
  resources: readonly Resource[];
}> = ({resources}) => {
  const categoryTree = useMemo(() => {
    if (!resources) {
      return {};
    }
    let tree: {[index: string]: {[index: string]: Resource[]}} = {};
    for (const resource of resources) {
      let categorySubtree = tree[resource.category];
      if (!categorySubtree) {
        tree[resource.category] = categorySubtree = {};
      }
      const subcategory = resource.subcategory ?? "";
      let subcategorySubtree = categorySubtree[subcategory];
      if (!subcategorySubtree) {
        categorySubtree[subcategory] = subcategorySubtree = [];
      }
      subcategorySubtree.push(resource);
    }
    return tree;
  }, [resources]);

  return (
    <Container fluid>
      {Object.keys(categoryTree)
        .sort()
        .map((category, categoryI) => (
          <Row className={categoryI > 0 ? "mt-4" : undefined} key={categoryI}>
            <Col className="px-0">
              <h5>{category}</h5>
              {Object.keys(categoryTree[category])
                .sort()
                .map((subcategory, subcategoryI) => (
                  <Fragment key={subcategoryI}>
                    {subcategory.length > 0 ? (
                      <strong>{subcategory}</strong>
                    ) : null}
                    <ResourcesTable
                      resources={categoryTree[category][subcategory]}
                    />
                  </Fragment>
                ))}
            </Col>
          </Row>
        ))}
    </Container>
  );
};
