import {ResourceJson} from "~/models/json/ResourceJson";
import {resourceJsonSchema} from "~/models/json/resourceJsonSchema";
import {RootModel} from "~/models/RootModel";
import {PostalAddress} from "~/models/PostalAddress";
import {Memoize} from "typescript-memoize";

export class Resource extends RootModel {
  private constructor(private readonly json: ResourceJson) {
    super();
  }

  @Memoize()
  get address(): PostalAddress | undefined {
    return this.json.address ? new PostalAddress(this.json.address) : undefined;
  }

  get category(): string {
    return this.json.category;
  }

  get description(): string | undefined {
    return this.json.description;
  }

  get email(): string | undefined {
    return this.json.email;
  }

  static fromJson(json: any): Resource {
    return new Resource(resourceJsonSchema.validateSync(json));
  }

  get id(): string {
    return this.json.id;
  }

  get name(): string {
    return this.json.name;
  }

  get openingHours(): string | undefined {
    return this.json.openingHours;
  }

  get subcategory(): string | undefined {
    return this.json.subcategory;
  }

  get telephone(): string | undefined {
    return this.json.telephone;
  }

  get url(): string | undefined {
    return this.json.url;
  }
}
