import * as React from "react";
import {useEffect, useState} from "react";
import {useQueryParam} from "use-query-params";
import {Col, Container, Row} from "reactstrap";
import {SearchForm} from "~/components/SearchForm";
import {ResourceIndex} from "~/applications/resources/ResourceIndex";
import {Resource} from "~/models/Resource";
import {ResourcesTree} from "~/applications/resources/ResourcesTree";

export const ResourcesApplication: React.FunctionComponent<{
  readonly resourceIndex: ResourceIndex;
}> = ({resourceIndex}) => {
  const [resources, setResources] = useState<readonly Resource[] | null>(null);
  const [query, setQuery] = useQueryParam<string | undefined>("query");

  useEffect(() => {
    if (query) {
      setResources(resourceIndex.search(query));
    } else {
      setResources(resourceIndex.resources);
    }
  }, [resourceIndex, query]);

  if (!resources) {
    return null;
  }

  return (
    <Container fluid className="px-0">
      <Row>
        <Col className="ms-auto" xs={4}>
          <SearchForm onSearch={setQuery} />
        </Col>
      </Row>
      <Row>
        <Col className="px-0">
          <ResourcesTree resources={resources} />
        </Col>
      </Row>
    </Container>
  );
};
