import * as React from "react";
import {useMemo} from "react";
import {Document} from "~/models/Document";
import DataTable, {IDataTableColumn} from "react-data-table-component";

export const DocumentsTable: React.FunctionComponent<{
  documents: readonly Document[];
}> = ({documents}) => {
  const dataTableColumns = useMemo((): IDataTableColumn<Document>[] => {
    const columns: IDataTableColumn<Document>[] = [];
    if (documents.some((document) => !!document.author)) {
      columns.push({
        name: "Author",
        selector: (document) => document.author,
        sortable: true,
      });
    }
    columns.push({
      cell: (document) => (
        <a href={document.url} target="_blank" title={document.abstract}>
          {document.title ?? document.id}
        </a>
      ),
      name: "Title",
      selector: (document) => document.title ?? document.id,
      sortable: true,
    });
    if (documents.some((document) => !!document.datePublished)) {
      columns.push({
        format: (document) => document.datePublished!.toLocaleString(),
        name: "Date published",
        selector: (document) => document.datePublished,
        sortable: true,
      });
    }
    if (documents.some((document) => !!document.temporalCoverage)) {
      columns.push({
        format: (document) => document.temporalCoverage!.toLocaleString(),
        name: "Meeting date",
        selector: (document) => document.temporalCoverage,
        sortable: true,
      });
    }
    return columns;
  }, [documents]);

  return (
    <DataTable
      columns={dataTableColumns}
      data={documents.concat()}
      // noContextMenu={true}
      noHeader={true}
      pagination={true}
      striped={true}
    />
  );
};
